<template>
  <div id="app">
    <header>
      <div class="mainPart">
        <img src="../static/logo.gif" class="logo">
      </div>
    </header>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      activeIndex: "1",
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  }
};
</script>

<style lang="scss">
header {
  padding: 0 20px;
  height: 100px;
  background-color: black;
  line-height: 100px;
  border-bottom: 1px solid rgba(237, 237, 237, 0.25);
  .mainPart {
    width: 12.8rem;
    margin: 0 auto;
    font-size: 0;
    .logo {
      height: 100px;
      width: 100px;
    }
    .menuBar {
      font-size: 12px;
      width: calc(100% - 100px);
      display: inline-block;
      height: 100px;
      .el-menu-demo{
        height: 100%;
        .el-menu-item{
          height: 100%;

        }
      }
    }
  }
}
</style>



