import Vue from 'vue'
import Router from 'vue-router'
import uploader from '@/components/uploader'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'uploader',
      component: uploader
    }
  ],
  mode:'history'
})



