<template>
  <div>
    <div class="compressContent">
      <ul>
        <li>
          <p>选择压缩参数</p>
        </li>
        <li v-show="size">
          <el-select v-model="size" placeholder="压缩后尺寸" class="gapSelector">
            <el-option v-for="item in sizeGoupe" :key="item.value" :value="item.value" :label="item.size">
            </el-option>
          </el-select>
        </li>
        <li v-show="!size">
          <el-input v-model="width" type="text" placeholder="宽" class="sizeInput"></el-input>
          <el-input v-model="height" type="text" placeholder="高" class="sizeInput"></el-input>
          <i class="el-icon-error cursorPoint" @click="backToSelectSize"></i>
        </li>
        <li>
          <el-select v-model="gap" placeholder="截取间隔帧数" class="gapSelector">
            <el-option v-for="item in gapNumberGoupe" :key="item.value" :value="item.value"
            :label="item.gap">
            </el-option>
          </el-select>
        </li>
        <li class="qualityTitle">
          压缩后图片质量
        </li>
        <li class="sliderLi">
          <div class="block sliderBox">
            <el-slider v-model="quality" :format-tooltip="formatTooltip"></el-slider>
          </div>
        </li>
      </ul>
      <div class="main">
        <div class="uploader">
          <div class="description">
            <img src="../../static/uploadIcon.png" alt="">
            <p>直接拖拽gif到此,或者点击上传按钮</p>
            <span>可多个上传,单个文件最大20MB</span>
          </div>
          <input type="file" class="uploaderInput" multiple  @change="onFileChange">
        </div>
      </div>
      <span v-show="uploading">上传中</span>
      <div class="choicedItem">
        <ul>
          <li v-for="(item,index) in uploadedFile" :key="index">
            <img :src="item.url||item.path" alt="" style="height:100px;width:100px;">
            <a href="javascript:;" class="downloadLink" @click="downloadFile(item.url)" >点击下载</a>
            <span class="fileSize">原始文件大小{{(item.originSize/1024).toFixed(0)+"Kb"}}</span>
            <span class="fileSize">压缩后文件大小{{(item.size/1024).toFixed(0)+"Kb"}}</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      files: [],
      gapNumberGoupe: [
        {
          gap: "每帧截取",
          value: 1
        },
        {
          gap: "每2帧截取一次画面",
          value: 2
        },
        {
          gap: "每3帧截取一次画面",
          value: 3
        },
        {
          gap: "每4帧截取一次画面",
          value: 4
        },
        {
          gap: "每5帧截取一次画面",
          value: 5
        }
      ],
      sizeGoupe: [
        {
          size: "200*200(表情包最佳)",
          value: "200x200"
        },
        {
          size: "100*100(小)",
          value: "100x100"
        },
        {
          size: "150*150(略小)",
          value: "150x150"
        },
        {
          size: "300*300",
          value: "300x300"
        },
        {
          size: "350*350",
          value: "350x350"
        },
        {
          size: "自定义",
          value: ""
        }
      ],
      uploading: false,
      gap: 1,
      size: "200x200",
      quality: 50,
      uploadedFile: [],
      height: "",
      width: ""
    };
  },
  methods: {
    jugleIsGif(files) {
      let flag = true;
      for (const key in files) {
        if (files.hasOwnProperty(key)) {
          const obj = files[key];
          if (obj.type != "image/gif") {
            flag = false;
          }
        }
      }
      return flag;
    },
    backToSelectSize() {
      this.size = "200x200";
    },
    downloadFile(url) {
      window.location.href = url;
    },
    formatTooltip(val) {
      return val + "%";
    },
    async onFileChange(e) {
      if (this.uploading) {
        alert("文件正在上传处理中,请在稍后再试");
        e.target.value = "";
        return;
      }
      let files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      if (!this.jugleIsGif(files.type) ) {
        alert("请上传gif图片");
        return;
      }
      this.uploading = true;
      let fileList = [...files];
      e.target.value = "";
      let originFiles = [...this.files];
      this.files = fileList;
      try {
        let result = await this.uploadFile(fileList);
        this.uploading = false;
        let images = result.data.imageUrls;
        images.forEach(object => {
          this.uploadedFile.push(object);
        });
      } catch (error) {
        this.files = [...originFiles];
        this.uploading = false;
        alert(error);
      }
    },
    calculateQuality() {
      return this.quality;
    },
    testawait() {
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          resolve();
        }, 1000);
      });
    },
    uploadFile(files) {
      return new Promise((resolve, reject) => {
        let formData = new FormData();
        formData.append("gap", this.gap);
        formData.append("quality", this.calculateQuality());
        if (this.size) {
          formData.append("size", this.size);
        } else {
          formData.append("size", this.width + "x" + this.height);
        }
        files.forEach(file => {
          formData.append(file.name, file);
        });
        this.$http
          .post("uploadGif", formData, {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          })
          .then(
            respons => {
              let res = respons.body;
              if (res.code == 0) {
                resolve(res);
              } else {
                reject(res.message);
              }
            },
            error => {
              reject("请求失败");
            }
          );
      });
    }
  },
  computed: {}
};
</script>
<style lang="scss" scoped>
.compressContent {
  padding: 40px 20px;
  border-radius: 19px;
  color: #fff;
  background-color: #2b2d37;
  width: 1030px;
  margin: 30px auto;
  > ul {
    overflow: hidden;
    > li {
      height: 60px;
      float: left;
      position: relative;
      margin-left: 20px;
      .gapSelector {
        margin-top: 10px;
        color: #333;
      }
      .sizeInput {
        width: 80px;
        margin-top: 10px;
      }
      .cursorPoint {
        cursor: pointer;
      }
      p {
        font-size: 18px;
      }
    }
    .sliderLi {
      margin-left: -20px;
    }
  }
}

.el-slider {
  float: right;
  width: 70%;
  margin-right: 20px;
}

.sliderBox {
  width: 200px;
  overflow: hidden;
  padding-top: 11px;
}

.qualityTitle {
  line-height: 60px;
  margin-left: 40px !important;
}

.demonstration {
  display: inline-block;
}

.main {
  position: relative;
  overflow: hidden;
  .uploader {
    position: relative;
    width: 738px;
    height: 244px;
    margin: 20px auto;
    margin-bottom: 40px;
    border-radius: 19px;
    border: 10px dashed #fff;
    box-sizing: border-box;
    animation-name: smallerSubmitRegionAnim;
    text-align: center;
    .uploaderInput {
      height: 100%;
      width: 100%;
      position: absolute;
      opacity: 0;
      top: 0;
      left: 0;
    }
    .description {
      padding-top: 30px;
      width: 100%;
      height: 90%;
      span {
        font-size: 14px;
        color: #999;
      }
    }
  }
}
.flag {
  display: block;
  text-align: right;
  width: 800px;
  margin: 0 auto;
}
.choicedItem ul li {
  width: 800px;
  margin: 0 auto;
  padding-right: 200px;
  padding: 20px 0;
}
.loadingImage {
  width: 80px;
}
.fileSize {
  margin-left: 50px;
}
.downloadLink {
  color: #ccc;
}
</style>



